@charset 'UTF-8';
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font: 12px Arial;
  margin: 0px;
  padding: 0px;
}

:root {
  --main-confirmate: #63ace7;
  --main-recuperari: #00ffff;
  --main-decese: #f3eaea;
  --main-barbat: #6c4e97;
  --main-femeie: #d4855a;
  --main-null: #fff;
}

h1 {
  text-align: center;
  font: 16px sans-serif;
}

.up-title {
  text-align: center;
  position: absolute;
  top: 0%;
  width: 100%;
}

.wrapper {
  margin: 0 20;
  margin-left: auto;
  margin-right: auto;
  overflow-y: auto;
}

svg.chart-group {
  width: 100%;
  max-width: 1600px;
  font: 12px sans-serif;
}

.link {
  fill: none;
  stroke: #999;
  stroke-width: 1px;
  cursor: pointer;
}

#licensing {
  fill: green;
}

.link.licensing {
  stroke: green;
}

.link.resolved {
  stroke-dasharray: 0, 2 1;
}

.node {
  stroke-linecap: round;
  stroke-linejoin: round;
}

text {
  font: 11px sans-serif;
  pointer-events: none;
}

.node-labels {
  user-select: none;
  stroke-width: 3px;
  text-shadow: 0 1px 0 #fff, 1px 0 0 #fff, 0 -1px 0 #fff, -1px 0 0 #fff;
}

.labels {
  font: 8px sans-serif;
  text-anchor: middle;
}

.node-labels.hidden,
.labels.hidden {
  display: none;
}

.node--hover circle {
  stroke: #000;
  stroke-width: 1.2px;
}

#spinner {
  max-width: 600px;
  text-align: center;
  position: absolute;
  right: 120px;
  top: 110px;
  margin: auto;
}

.grabbable {
  cursor: move; /* fallback if grab cursor is unsupported */
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
}

/* Apply a 'closed-hand' cursor during drag operation. */
.grabbable:active {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}

circle {
  stroke: #333;
  stroke-width: 1px;
  cursor: pointer;
}

.land {
  fill: #e8e8e8;
  stroke: #fff;
  stroke-width: 2;
}

.land:hover {
  fill: #d1cfcf;
}

.title {
  font-size: 16px;
  text-decoration: underline;
  text-anchor: middle;
}

button {
  padding: 10;
}

svg.chart-group a {
  color: #0078a8;
}

.side-pannel-left,
.side-pannel-right {
  z-index: 100;
  font-size: 12px;
  pointer-events: none;
}

.side-pannel-left {
  position: absolute;
  left: 10px;
  top: 0;
}

.side-pannel-right {
  position: absolute;
  right: 10px;
  top: 0;
}

.control {
  margin-right: 10px;
  margin-top: 10px;
  cursor: auto;
  float: left;
  clear: both;
  position: relative;
  z-index: 800;
  pointer-events: visiblePainted;
  pointer-events: auto;
}

.control a {
  text-decoration: none;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  display: block;
  height: 36px;
  width: 36px;
  line-height: 36px;
  background-size: contain;
  cursor: pointer;
}

.control a:hover {
  background-color: #f4f4f4;
}

.control-group a {
  background-color: #fff;
  border-bottom: 1px solid #ccc;
  display: block;
  text-align: center;
  text-decoration: none;
  color: black;
}

.control-group a:first-child {
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}
.control-group a:last-child {
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

