#search-input {
  width: 50px;
}

.slider {
  position: absolute;
  left: 45px;
  top: 0;
  right: 62px;
  margin-top: 10px;
  z-index: 100;
  text-align: center;
  padding: 5px;
}

.range {
  width: 100%;
}

.county-legend,
.status-legend,
.gender-legend,
.age-legend {
  font-size: 10px;
  position: absolute;
  left: 10px;
  top: 55px;
  z-index: 100;
  padding: 5px;
  overflow-y: auto;
}

.gender-legend {
  height: 30%;
}

.status-legend {
  height: 40%;
}

.age-legend {
  height: 50%;
}

.county-legend {
  height: 90%;
}

.hide {
  position: absolute;
  left: -9999px;
}

tooltip_div.tooltip {
  /* text-align: center; */
  position: absolute;
  color: #222;
  padding: 10px;
  font-size: 0.7em;
  line-height: 12pt;
  font-family: "EB Garamond", serif;
  max-width: 160px;
  z-index: 1000;
}

.tooltip-abs {
  right: 70px;
  top: 55px;
}

tooltip_div,
.county-legend,
.status-legend,
.gender-legend,
.age-legend,
.control {
  -moz-border-radius: 5px;
  border-radius: 5px;
  background: none no-repeat scroll center center #fff;
  border: 1px solid #bbb;
  opacity: 0.9;
}

.bubble {
  fill-opacity: 0.3;
  stroke: black;
  stroke-width: 0.5;
  vector-effect: non-scaling-stroke;
}
.bubble:hover {
  fill-opacity: 0.5;
}

/* Smaller devices */
@media (max-width: 360px) {
  .side-pannel-left {
    top: 0;
    left: 5px;
  }

  .side-pannel-right {
    top: 0;
    right: 0;
  }

  .control #search-input {
    width: 36px;
  }

  .land {
    stroke-width: 1;
  }
}

/* Small devices (tablets, 768px and up) */
@media (min-width: 550px) {
  .slider {
    left: 50px;
    top: 0;
    right: 75px;
  }

  .tooltip-abs {
    right: 85px;
    top: 55px;
  }

  .wrapper {
    width: 97%;
  }
}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: 850px) {
  .slider {
    left: 60px;
    top: 0;
    right: 80px;
  }

  .wrapper {
    width: 97%;
    float: none;
  }

  .control a {
    width: 50px;
    line-height: 50px;
  }
}
